@import "styles/variables";
@import "styles/mixins";


.button {
  @include resetButton;

  width: 100%;
  transition-duration: 0.3s;
  cursor: pointer;

  &.buttonFilled {
    @include capitals-bold($colorMainBg);

    padding: 2.2rem 0 1.8rem 0;
    background-color: var(--color-highlight-buttons);
  }

  &.buttonDecorated {
    @include capitals-bold(var(--color-highlight-buttons));

    position: relative;
    padding: 2rem 0;
    border: 0.1rem solid var(--color-highlight-buttons);
    border-bottom-width: 0.5rem;
    overflow: hidden;
    z-index: 0;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% + 0.4rem);
      background-color: var(--color-highlight-buttons);
      transition-duration: 0.3s;
      transform: translateX(-100%);
      z-index: -1;
    }

    &:hover {
      color: $colorMainBg;

      &::before {
        transform: translateX(0);
      }

      .icon {
        stroke: $colorMainBg;
        transform: translateX(1rem);
      }
    }

    .icon {
      margin-left: 1rem;
      width: 1.6rem;
      stroke: var(--color-highlight-buttons);
      transition-duration: 0.3s;
      transform: translateX(0);
    }
  }

  &.buttonGradient {
    @include capitals-bold($colorMainBg);

    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4.6rem 4.3rem 4.4rem 5rem;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-highlight-mosaic);
      z-index: -1;
      transition-duration: 0.3s;
      transform: translateX(-29rem);
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-highlight-mosaic);
      z-index: -1;
      opacity: 0.5;
    }

    .icon {
      margin-left: 1rem;
      width: 1.6rem;
      stroke: $colorMainBg;
      transition-duration: 0.3s;
      transform: translateX(0);
    }
  }
}

@media (max-width: $widthMobileMax) {
  .button{
    &.buttonDecorated {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem 2.3rem 2rem 3rem;
        text-align: left;

      .icon {
        margin-left: auto;
      }
    }
  }
}

@media (min-width: $widthDesktopMin) {
  .button {
    &.buttonGradient {
      &:hover {
        &::before {
          transform: translateX(0);
        }
      }
    }
  }
}
